.containerStyle{
    background-color: $white-background-color;
    width: 100%;
    border: 1px solid $light-border-color;
    border-bottom: 1px solid $bottom-border-color;
    border-radius: 5px;
    box-sizing: border-box;
    box-shadow: 0 3px 2px -2px rgba(200,200,200,0.2);
    margin-bottom: 10px;
    padding: 10px;
    padding-bottom: 15px;
    text-align: left;
}

.overlayContainer{
    @extend .containerStyle;
    width: 50%;
    display: flex;
    flex-direction: column;
    z-index: 10000;
}

.lightThemeButton{
    background-color: $light-theme-color;
    color: $theme-color;
    height: 30px;
    border: 1px solid $theme-color;
    border-radius: 15px;    
    padding: 0px 10px   
}

._username{
    &:hover{
        text-decoration: underline;
        cursor: pointer;
    }
}